@import url(https://fonts.googleapis.com/css2?family=Marcellus&family=Nunito&family=Outfit:wght@200&display=swap);
.aboutDiv{
    text-align: center;
}
.aboutParagraph{
    color: aliceblue;
    font-size: 22px;
    font-family: 'Nunito', sans-serif;
    text-align: justify;
    margin: auto;
    width: 50rem;
}

.swiper {
    width: 600px;
    height: 600px;
    width: 50%;
    display:block;
  }

  .swiperImage{
    height: 50%;
    width: 50%;
}

.swiper-slide{
    width: auto;
    height: auto;
}
a{
    color: rgb(128, 195, 255);
}
.PERNPitLink{
    color: aliceblue;
}

.card{
    display: block;
    border-radius: 10px;
    width:-webkit-max-content;
    width:max-content;
    /* margin: 1rem */
    margin-top: .5rem;
}


.cardPhoto{
    width: 180px;
    height: 180px;
    object-fit: cover;
    border-radius: 12%;
    border: 3px solid aliceblue;
}

.cardName{
    color: aliceblue;
    margin: 5px
}

.homeDiv{
    display: flex;
    flex-wrap: wrap;
justify-content: center;
z-index: -1
}

.occupation{
    font-family: 'Aboreto', cursive;
    border-top: 2px solid black;
    border-bottom: 2px solid black;
    margin: 1rem
}

a {
    text-decoration: none;
}

a:hover{
    color:  #BF0603;
    outline-color: solid black;
    font-weight:300;
}

button{
    color: aliceblue;
    font-family: 'Nunito', sans-serif;
    background-color: #BF0603;
    font-size: 22px;
    margin: .5rem;
    border-radius: 10px;

}

button:hover{
cursor: pointer;
color:#BF0603;
background-color: aliceblue;
}

div{
    color: aliceblue;
    font-size: 22px;
    font-family: 'Nunito', sans-serif;  
    display:flexbox;
    text-align: center; 
}

.videoDiv{
    padding: 1rem;
    text-align: center;
}

.iframe{
    padding: 1rem;
}

.extermalLink{
    -webkit-text-decoration-line: underline;
            text-decoration-line: underline;
    text-align: center;
    color: aliceblue;
}

.extermalLink:hover{
    color: #BF0603
}


h1{
    color:#BF0603;
    text-align: center;
    font-family: 'Aboreto', cursive;
    font-size: 30px;
}

html{
    background-color:
     /* #001B2E */
