     #2E4057
}

.icon{
    color: aliceblue;
    
}
.icon:hover{
    color: #BF0603;
}

#iconDiv{
    display: grid;
    color: aliceblue;
    position: fixed;
    margin-top: 15rem;
    margin-left: 1rem;
    font-size: 3rem;
}

input{
    border-radius: 5px;
    width: 15rem
}

label{
    color: aliceblue;
    font-size: 22px;
    font-family: 'Nunito', sans-serif;
    text-align: center;    
}

.navLink{
    font-family: 'Outfit', sans-serif;
    font-size: 30px;
    color: aliceblue;
}
p{
    color: aliceblue;
    font-size: 22px;
    font-family: 'Nunito', sans-serif;
    text-align: center;
}

select{
  border-radius: 5px;
  text-align: center;
  font-family: 'Nunito', sans-serif;
  font-size: 22px
  };

textarea{
    resize: none;
}
.heading{
    font-family: 'Aboreto', cursive;
    font-size: 44px;
    margin: auto;
    color: #BF0603;
    padding-top: 1rem;
    padding-bottom: 1rem;

}

.navLink{
    padding-left: .25rem;
    padding-right: .25rem;
    margin: 1rem;
    position: relative;
}

.navbardiv{
    border-radius: 10px;
    border-bottom: solid aliceblue;
